.menu-toggle {
  padding: 25px 0;
  padding: 2.5rem 0;
  cursor: pointer;
  background-color: transparent;
  display: block;
  position: relative;
  line-height: 1;
  color: #333333;
}

.menu-toggle:hover {
  background: none;
  outline: none;
  box-shadow: none;
}

.menu-toggle:focus {
  outline: 1px solid #eeeeee;
  background: none;
}

@media (min-width: 896px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle span {
  display: inline-block;
  position: relative;
  height: 2.5px;
  background: #333333;
  transition: background 0s 0.25s;
  width: 25px;
  width: 2.5rem;
  float: right;
  margin-left: 0.236em;
  margin-top: 7px;
  border-radius: 0.1rem;
}

.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2.5px;
  background-color: #333333;
  border-radius: 0.1rem;
  content: " ";
}

.menu-toggle span::before {
  top: -7px;
  transition: top 0.25s 0.25s, transform 0.25s 0s;
}

.menu-toggle span::after {
  bottom: -7px;
  transition: bottom 0.25s 0.25s, transform 0.25s 0s;
}

.menu-toggle.activated span {
  background: none;
}

.menu-toggle.activated span::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 0.25s 0s, transform 0.25s 0.25s;
}

.menu-toggle.activated span::after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 0.25s 0s, transform 0.25s 0.25s;
}

.sub-menu-toggle {
  position: absolute;
  right: 0;
  background: none;
  color: #333333;
  padding: 1em;
  margin: 0 -1em;
  z-index: 1;
  border: none;
  font-size: 1em;
}

@media (min-width: 896px) {
  .sub-menu-toggle {
    display: none;
  }
}

.sub-menu-toggle:hover, .sub-menu-toggle:focus {
  background: none;
}

.sub-menu-toggle:before {
  content: "";
  display: block;
  margin: 2px 0 -2px;
  margin: 0.2rem 0 -0.2rem;
  border: 4px solid transparent;
  border: 0.4rem solid transparent;
  border-top-color: #333333;
}

.sub-menu-toggle.activated:before {
  transform: rotate(180deg);
  margin: -2px 0 2px;
  margin: -0.2rem 0 0.2rem;
}

