/* ====== Gravity Forms ====== */
.gform_heading,
.gform_body,
.gform_footer {
  clear: both;
}

.gform_heading {
  padding-bottom: 1em;
}

.gform_wrapper .gform_fields {
  list-style: none;
  margin: 0 -10px;
  margin: 0 -1rem;
  padding: 0;
}

.gform_wrapper .gform_fields li {
  list-style-type: none;
}

.gform_wrapper .gfield_checkbox,
.gform_wrapper .gfield_radio {
  list-style: none;
  padding: 0;
}

.ginput_container span {
  margin-bottom: 10px;
  margin-bottom: 1rem;
}

.ginput_container input,
.ginput_container select,
.ginput_container textarea {
  width: 100%;
}

.ginput_container input,
.ginput_container textarea {
  margin: 0;
}

.gfield_label {
  display: inline-block;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gfield_label:empty {
  display: none;
}

.left_label .gfield_label {
  float: left;
}

.right_label .gfield_label {
  float: right;
}

.gfield_required {
  padding-left: 0.1em;
}

.gfield_checkbox li,
.gfield_radio li {
  width: 100%;
}

.gfield_checkbox input,
.gfield_radio input {
  width: auto;
  float: left;
  margin-right: 10px;
  margin-right: 1rem;
}

.gfield_checkbox label,
.gfield_radio label {
  float: left;
  line-height: 1;
}

.gsection {
  margin: 10px 0;
  margin: 1rem 0;
}

.gsection h2 {
  border-bottom: 1px solid #eeeeee;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gf_progressbar_wrapper {
  padding-bottom: 15px;
}

.gf_progressbar {
  background: gray;
}

.gf_progressbar_percentage {
  background: red;
  padding: 5px 10px;
  padding: 5px 1rem;
  text-align: right;
}

.gfield_time_hour,
.gfield_time_minute,
.gfield_time_ampm {
  display: inline;
  display: inline-block;
}

.gfield_time_hour label,
.gfield_time_minute label {
  display: block;
}

.gfield_time_ampm {
  vertical-align: top;
}

.validation_error {
  font-size: 1.2em;
  font-weight: 700;
  padding-bottom: 15px;
}

.gfield {
  width: 100%;
  list-style-type: none;
  clear: both;
  overflow: auto;
  padding: 0 10px 15px;
  padding: 0 1rem 1.5rem;
  margin: 0;
}

.gfield.gfield_error {
  background: #FFDFE0;
  margin-bottom: 15px;
  padding-top: 15px;
}

.gfield.gfield_error .gfield_label {
  font-weight: 700;
}

.gfield .validation_message {
  font-weight: 700;
  margin-top: 10px;
  margin-top: 1rem;
}

.gfield li {
  list-style-type: none;
}

.gform_validation_container {
  display: none;
}

.gform_footer .gform_button {
  width: auto;
}

